.wp-block-sonrieme-blocks-service-card {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  border-radius: 16px;
  overflow: hidden;
  background-size: cover;
  background-position: center; }
  .wp-block-sonrieme-blocks-service-card.no-image-set:before {
    display: none; }
  .wp-block-sonrieme-blocks-service-card:before {
