    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(26, 38, 64, 0.4); }
  .wp-block-sonrieme-blocks-service-card .components-placeholder.block-editor-media-placeholder.editor-media-placeholder {
