    .wp-block-sonrieme-blocks-service-card .editor-rich-text.block-editor-rich-text > h3.is_selected {
      color: #ffffff; }
      .wp-block-sonrieme-blocks-service-card .editor-rich-text.block-editor-rich-text > h3.is_selected [data-rich-text-placeholder] {
        color: #ffffff; }
